import IndexList from 'base/indexlist/indexlist'
import cityData from '../../data/index-list.json'
import Page from 'base/page/page.vue'
import { mapGetters, mapActions } from 'vuex'
export default {
  components: {
    IndexList,
    Page
  },
  data() {
    return {
      title: '选择城市',
      cityData: cityData
    }
  },
  computed: {
    data() {
      let ret = []
      this.cityData.forEach((cityGroup) => {
        let group = {}
        group.name = cityGroup.name
        group.items = []
        cityGroup.cities.forEach((city) => {
          let item = {}
          item.name = city.name
          item.value = city.cityid
          group.items.push(item)
        })
        ret.push(group)
      })
      return ret
    }
  },
  methods: {
    selectItem(item) {
      this.saveCity(item.name)
      this.$router.back()
    },
    clickTitle(title) {
      console.log(title)
    },
    ...mapActions([
      'saveCity'
    ])
  },
}




